.header-color {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #0f0c29; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  color: white;
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.projects-grid {
  display: flex;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: "Anton", sans-serif;
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

.contact-list .item-content {
  width: 100%;
  font-size: 30px;
  font-family: "Anton";
}

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

.text-align-center {
  text-align: center;
}

.resume-avatar {
  height: 300px;
  border-radius: 50%;
}

.ed-ex-det {
  font-size: 13px !important;
  font-style: italic;
}

.lh-16 {
  line-height: 16px;
}

.lh-32 {
  line-height: 32px;
}

.mb-0 {
  margin-bottom: 0px;
}

.mb-1 {
  margin-bottom: 1em;
}

.mt-0 {
  margin-top: 0px;
}

.mt-1 {
  margin-top: 1em;
}

.pb-0 {
  padding-bottom: 0px;
}

.pb-1 {
  padding-bottom: 1em;
}

.pb-2 {
  padding-bottom: 2em;
}

.pt-0 {
  padding-top: 0px;
}

.pt-1 {
  padding-top: 1em;
}

.pt-2 {
  padding-top: 2em;
}

.text-grey {
  color: grey;
}

.resume-hr {
  border-top: 3px solid;
}

.resume-hr.avatar {
  border-color: #833fb2;
  width: 50%;
}

.resume-hr.details {
  border-color: #e22947;
}

.d-flex {
  display: flex;
}

.m-auto {
  margin: auto;
}

.w-75 {
  width: 75%;
}
.w-90 {
  width: 90%;
}

.w-25 {
  width: 25%;
}

.w-10 {
  width: 10%;
}

.header-link {
  text-decoration: none;
}

.text-white {
  color: white;
}

.text-black {
  color: black;
}

.hidden {
  display: hidden;
}
